/* side nav collapse threshold */



@media (max-width: 767px)
{
  /* since 767px is default for bootstrap 3.3.7, we need this other than 767px */
  

  /* enable top nav bar */
  .top-nav {
    display: block;
  }

  /* disable side nav */
  :root {
    --side-nav-width: 0px;
    --top-nav-height: var(--top-nav-height-def);
  }

  /* disable side nav */
  #side-nav-container {
    left: calc(-1 * var(--side-nav-width-def));
    right: unset;
    box-shadow: -5px 0 20px #00000024;
  }


  .upside-down-tabs {
    left: 0px;
    width: 100%;
  }





  .color_scheme_switch_top_holder {
    /* fixed to top nav */
    position: absolute;
    display: inline;
    top: 14px;
    right: calc(var(--main-container-margin) + 60px);
  }





  #toc-container {
    display: none;
  }


}

.footer-text>a ,.side-nav-footer p ,.footer-text p {display: none;}